<template>
  <div
    :style="{position: 'absolute',zIndex: zindex,bottom: '0px',left: '0px',right: '0px',background: '#fff'}"
    class="luckysheet-datavisual-quick-m"
    v-if="showList"
  >
    <el-button
      @click="$emit('closeChartShowList')"
      plain
      round
      size="small"
      style="width:100%;margin:2px 4px 8px 4px;"
      type="danger"
    >
      <i class="iconfont icon-guanbi" style="float:left;"></i>
      {{ close }}
    </el-button>

    <el-radio-group size="mini" style="display:block;text-align:center;" v-model="currentPro">
      <el-radio-button :key="index" :label="item.type" v-for="(item,index) in config">{{item.name}}</el-radio-button>
    </el-radio-group>
    <div class="luckysheet-datavisual-quick-menu" id="luckysheet-datavisual-quick-menu">
      <div
        :data-type="item.type"
        :id="'luckysheet-datavisual-chart-menu-' + item.type"
        :key="index"
        @click="quickMenu"
        v-for="(item,index) in currentConfig.data"
      >
        <i :class="item.icon" aria-hidden="true" class="iconfont"></i>
        <span>{{item.name}}</span>
      </div>
    </div>
    <div
      @scroll="quickListScroll"
      class="luckysheet-datavisual-quick-list luckysheet-scrollbars"
      id="luckysheet-datavisual-quick-list"
    >
      <template v-for="(item,index) in currentConfig.data">
        <div :key="index" class="luckysheet-datavisual-quick-list-title">
          <a :data-type="item.type" :id="'luckysheet-datavisual-chart-listtitle-'+item.type">
            <i :class="item.icon" aria-hidden="true" class="iconfont"></i>
            {{item.name}}
          </a>
        </div>
        <div class="luckysheet-datavisual-quick-list-ul">
          <el-tooltip
            :content="chartItem.name"
            :key="index"
            :open-delay="500"
            effect="dark"
            placement="bottom"
            v-for="(chartItem,index) in item.data"
          >
            <div
              :chartAllType="chartItem.type"
              :class="chartItem.type==currentChartType? 'luckysheet-datavisual-quick-list-item-active':''"
              :data-style="chartItem.type.split('-')[2]"
              :data-type="chartItem.type.split('-')[1]"
              @click="changeChartType(chartItem.type)"
              class="luckysheet-datavisual-quick-list-item"
            >
              <img
                :src="chartItem.img.length==0?'data:image/svg+xml;base64,':chartItem.img"
                alt="图片"
              />
            </div>
          </el-tooltip>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import { mapState } from "vuex";
import { deepCopy } from "@/utils/util";
import { changeChangeAllType } from "@/utils/chartUtil";
import { chartreuse } from "color-name";
import transCN from "@/data/cn";
import transEN from "@/data/en";

//图表选择组件
export default {
  name: "ChartList",
  props: {
    showList: {
      type: Boolean,
      default: false,
    },
    zindex: {
      type: Number,
      default: 10,
    },
    currentChartType: {
      type: String,
      default: "echarts|line|default",
    },
    lang: {
      type: String,
      default: "cn",
    },
  },
  data: function () {
    return {
      config: [
        {
          type: "echarts",
          name: "echarts",
          data: [],
        },
      ],
      currentConfig: [],
      currentPro: null,
      chartAllType: "",
      list_scroll_direction: -1000,
      // echarts中文
      echartsCN: [
        {
          type: "line",
          name: "折线图",
          icon: "icon-tubiaozhexiantu",
          data: [
            {
              type: "echarts|line|default",
              name: "折线图",
              img:
                "data:image/png;base64,",
            },
            {
              type: "echarts|line|smooth",
              name: "平滑折线图",
              img:
                "data:image/png;base64,",
            },
            {
              type: "echarts|line|label",
              name: "带标签的折线图",
              img:
                "data:image/png;base64,",
            },
          ],
        },
        {
          type: "area",
          name: "面积图",
          icon: "icon-fsux_tubiao_duijimianjitu",
          data: [
            {
              type: "echarts|area|default",
              name: "面积图",
              img:
                "data:image/png;base64,",
            },
            {
              type: "echarts|area|stack",
              name: "堆叠面积图",
              img:
                "data:image/png;base64,",
            },
          ],
        },
        {
          type: "column",
          name: "柱状图",
          icon: "icon-chart",
          data: [
            {
              type: "echarts|column|default",
              name: "柱状图",
              img:
                "data:image/png;base64,",
            },
            {
              type: "echarts|column|stack",
              name: "堆叠柱状图",
              img:
                "data:image/png;base64,",
            },
          ],
        },
        {
          type: "bar",
          name: "条形图",
          icon: "icon-fsux_tubiao_duijizhuzhuangtu1",
          data: [
            {
              type: "echarts|bar|default",
              name: "条形图",
              img:
                "data:image/png;base64,",
            },
            {
              type: "echarts|bar|stack",
              name: "堆叠条形图",
              img:
                "data:image/png;base64,",
            },
          ],
        },
        {
          type: "pie",
          name: "饼 图",
          icon: "icon-fsux_tubiao_nandingmeiguitu",
          data: [
            {
              type: "echarts|pie|default",
              name: "饼 图",
              img:
                "data:image/png;base64,",
            },
            {
              type: "echarts|pie|split",
              name: "分离型饼图",
              img:
                "data:image/png;base64,",
            },
            {
              type: "echarts|pie|ring",
              name: "环形饼图",
              img:
                "data:image/png;base64,",
            },
          ],
        },
      ],
      // echarts英文
      echartsEN: [
        {
          type: "line",
          name: "Line Chart",
          icon: "icon-tubiaozhexiantu",
          data: [
            {
              type: "echarts|line|default",
              name: "Basic Line Chart",
              img:
                "data:image/png;base64,",
            },
            {
              type: "echarts|line|smooth",
              name: "Smoothed Line Chart",
              img:
                "data:image/png;base64,",
            },
            {
              type: "echarts|line|label",
              name: "Line with labels",
              img:
                "data:image/png;base64,",
            },
          ],
        },
        {
          type: "area",
          name: "Area Chart",
          icon: "icon-fsux_tubiao_duijimianjitu",
          data: [
            {
              type: "echarts|area|default",
              name: "Basic Area Chart",
              img:
                "data:image/png;base64,",
            },
            {
              type: "echarts|area|stack",
              name: "Stacked Area Chart",
              img:
                "data:image/png;base64,",
            },
          ],
        },
        {
          type: "column",
          name: "Column Chart",
          icon: "icon-chart",
          data: [
            {
              type: "echarts|column|default",
              name: "Basic Column Chart",
              img:
                "data:image/png;base64,",
            },
            {
              type: "echarts|column|stack",
              name: "Stacked Column Chart",
              img:
                "data:image/png;base64,",
            },
          ],
        },
        {
          type: "bar",
          name: "Bar Chart",
          icon: "icon-fsux_tubiao_duijizhuzhuangtu1",
          data: [
            {
              type: "echarts|bar|default",
              name: "Basic Bar Chart",
              img:
                "data:image/png;base64,",
            },
            {
              type: "echarts|bar|stack",
              name: "Stacked Bar Chart",
              img:
                "data:image/png;base64,",
            },
          ],
        },
        {
          type: "pie",
          name: "Pie Chart",
          icon: "icon-fsux_tubiao_nandingmeiguitu",
          data: [
            {
              type: "echarts|pie|default",
              name: "Basic Pie Chart",
              img:
                "data:image/png;base64,",
            },
            {
              type: "echarts|pie|split",
              name: "Split Pie Chart",
              img:
                "data:image/png;base64,",
            },
            {
              type: "echarts|pie|ring",
              name: "Doughnut Chart",
              img:
                "data:image/png;base64,",
            },
          ],
        },
      ],
    };
  },
  mounted() {
    if (this.lang == "zh") {
      this.config[0].data = this.echartsCN
      this.close = "关闭图表选择菜单";
      this.chooseConfig();
      return;
    }
    this.config[0].data = this.echartsEN
    this.close = "close chart-select menu";
    this.chooseConfig();
  },
  computed: {
    ...mapState("chartSetting", ["chartLists", "currentChartIndex"]),

    chartPro() {
      return this.chartAllType.split("|")[0];
    },
    chartType() {
      return this.chartAllType.split("|")[1];
    },
    chartStyle() {
      return this.chartAllType.split("|")[2];
    }
  },
  watch: {
    currentChartType(val) {
      this.chartAllType = val;
    },
    lang(val) {
      if (this.lang == "zh") {
        this.config[0].data = this.echartsCN
        this.close = "关闭图表选择菜单";
        this.chooseConfig();
        return;
      }
      this.config[0].data = this.echartsEN
      this.close = "close chart-select menu";
      this.chooseConfig();
    },
  },
  methods: {
    chooseConfig() {
      for (var i = 0; i < this.config.length; i++) {
        if (this.config[i].type == this.chartPro) {
          this.currentConfig = this.config[i];
          return
        }
      }
      this.currentConfig = this.config[0];
    },
    changeChartType: function (value) {
      // 相同类型或者currentChartIndex为null不执行
      if (this.currentChartIndex == null) {
        return null;
      }
      if (
        (this.chartLists[this.currentChartIndex].chartOptions,
        chartreuse == value)
      ) {
        return null;
      }
      changeChangeAllType(
        deepCopy(this.chartLists[this.currentChartIndex].chartOptions),
        value
      );
      this.$emit('closeChartShowList')
      this.chartAllType = value;
    },
    quickListScroll: function (e) {
      var scrollTop = $(e.currentTarget).scrollTop();
      var _this = this;
      $(e.currentTarget)
        .find("div.luckysheet-datavisual-quick-list-title")
        .each(function () {
          var $t = $(this),
            position = $t.position();
          //console.log($t.find("a").data("type"),position.top, scrollTop);
          if (scrollTop >= _this.list_scroll_direction) {
            if (position.top + 55 > 0) {
              $("#luckysheet-datavisual-quick-menu div").removeClass(
                "luckysheet-datavisual-quick-menu-active"
              );
              $(
                "#luckysheet-datavisual-chart-menu-" + $t.find("a").data("type")
              ).addClass("luckysheet-datavisual-quick-menu-active");
              return false;
            }
          } else {
            if (position.top - 55 >= 0) {
              $("#luckysheet-datavisual-quick-menu div").removeClass(
                "luckysheet-datavisual-quick-menu-active"
              );
              var $obj = $t.prev().prev();
              if ($obj.length == 0) {
                $obj = $t;
              }
              $(
                "#luckysheet-datavisual-chart-menu-" +
                  $obj.find("a").data("type")
              ).addClass("luckysheet-datavisual-quick-menu-active");
              return false;
            }
          }
          setTimeout(function () {
            _this.list_scroll_direction = scrollTop;
          }, 0);
        });
    },
    quickMenu: function (e) {
      var $t = $(e.currentTarget);
      $("#luckysheet-datavisual-quick-menu div").removeClass(
        "luckysheet-datavisual-quick-menu-active"
      );
      $t.addClass("luckysheet-datavisual-quick-menu-active");
      var $scroll = $(
          "#luckysheet-datavisual-chart-listtitle-" + $t.data("type")
        ).parent(),
        p = $scroll.position().top;
      $("#luckysheet-datavisual-quick-list").scrollTop(
        p + 5 + $("#luckysheet-datavisual-quick-list").scrollTop()
      );
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.luckysheet-datavisual-quick-menu {
  width: 120px;
  overflow: auto;
  margin-top: 15px;
}
.luckysheet-datavisual-quick-menu::-webkit-scrollbar {
  display: none;
}

.luckysheet-datavisual-quick-menu > div {
  text-align: left;
  padding: 4px 4px;
  border-right: 3px solid #fff;
  color: #777;
  cursor: pointer;
  line-height: 1.4em;
  word-wrap: break-word;
  /*margin: 4px 0px;*/
}

.luckysheet-datavisual-quick-menu > div:hover {
  color: #000;
}

.luckysheet-datavisual-quick-menu > div i {
  width: 15px;
}

.luckysheet-datavisual-quick-menu > div:hover i {
  color: #ff7e7e;
}

.luckysheet-datavisual-quick-menu
  > div.luckysheet-datavisual-quick-menu-active {
  border-right: 3px solid #ff7e7e;
  color: #000;
  font-weight: bold;
}

.luckysheet-datavisual-quick-menu
  > div.luckysheet-datavisual-quick-menu-active:hover
  i {
  color: #000;
}

.luckysheet-datavisual-quick-range {
  padding: 5px 0px;
}

.luckysheet-datavisual-range-container {
  background: #fff;
  border: 1px solid #d9d9d9;
  border-top: 1px solid #c0c0c0;
  min-width: 20px;
  width: 100%;
  max-width: 200px;
  display: inline-block;
}

.luckysheet-datavisual-range-container-focus {
  border: 1px solid #4d90fe;
  box-shadow: inset 0px 1px 2px rgba(0, 0, 0, 0.3);
  outline: none;
}

.luckysheet-datavisual-range-input,
.luckysheet-datavisual-range-input:focus {
  background: transparent !important;
  border: none !important;
  box-sizing: border-box;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
  height: 25px;
  margin: 0;
  outline: none !important;
  padding: 1px 8px !important;
  width: 100%;
}

.luckysheet-datavisual-range-button-container {
  overflow: hidden;
  padding: 0 0 0 8px;
  text-align: right;
  width: 21px;
}

.luckysheet-datavisual-range-button-container div {
  padding: 2px 10px 0px 10px;
  font-size: 18px;
  cursor: pointer;
  color: #6598f3;
}

.luckysheet-datavisual-range-button-container div:hover {
  color: #ff7e7e;
}

.luckysheet-datavisual-range-config {
  /*display: inline-block;*/
}

.luckysheet-datavisual-quick-m {
  margin-top: 5px;
  min-height: 500px;
  top: 50px;
  font-size: 12px;
}

.luckysheet-datavisual-quick-list {
  left: 110px;
  right: 0px;
  bottom: 0px;
  top: 80px;
  position: absolute;
  overflow: auto;
  border-top: 1px solid #e5e5e5;
  padding: 5px 3px 35px 3px;
}

.luckysheet-datavisual-quick-list-title {
  padding: 4px 6px;
  background: #e5e5e5;
  margin-top: 10px;
}

.luckysheet-datavisual-quick-list-ul {
  overflow: hidden;
}

.luckysheet-datavisual-quick-list-item {
  display: inline-block;
  margin: 5px 8px;
  border: 1px solid #dadada;
  width: 100px;
  height: 80px;
}

.luckysheet-datavisual-quick-list-item:hover {
  border: 1px solid #ff7e7e;
  box-shadow: 0px 0px 20px #ff7e7e;
}

.luckysheet-datavisual-quick-list-item img {
  display: inline-block;
  width: 100px;
  height: 80px;
}

.luckysheet-datavisual-quick-list-item-active {
  border: 1px solid #6598f3;
  box-shadow: 0px 0px 20px #6598f3;
}
</style>
